.container {
    margin: 0 auto;
    max-width: 1200px;
}

.test-block {
    background: #478E64;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.25);
    padding-bottom: 100px;
}

.trafficlight-wrap {
    position: relative;
}

.trafficlight {
    background: url(../../icons/trafficlight.svg) center center/cover no-repeat;
    width: 111px;
    height: 208px;
    position: absolute;
    top: 101px;
    left: 70px;
}

.test-block-header {
    padding-top: 132px;
    text-align: center;
    font-weight: 700;
    font-size: 30px;
    line-height: 36px;
    color: #FFFFFF;
    position: relative;
}

.test-block-subheader {
    margin: 0 auto;
    width: 830px;
    padding: 20px 0 60px;
    font-weight: 600;
    font-size: 22px;
    line-height: 30px;
    text-align: center;
    color: #FFFFFF;
}

.test-card-practice {
    background: rgba(0, 0, 0, 0.1);
    border-radius: 50px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    -o-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}

.test-card-practice:hover {
    -o-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    background: rgba(0, 0, 0, 0.234);
}

.test-card-practice-img {
    background: url(../../img/practice-img.png) center center/cover no-repeat;
    width: 225px;
    height: 160px;
}

.test-card-practice-block {
    max-width: 840px;
}

.test-card-practice-header {
    margin-top: 25px;
    font-weight: 400;
    font-size: 30px;
    line-height: 36px;
    color: #FFFFFF;
    text-align: center;
}

.test-card-practice-text {
    margin: 20px;
    font-weight: 400;
    font-size: 22px;
    line-height: 27px;
    color: #FFFFFF;
}

.test-card-practice-text:last-child {
    margin-bottom: 85px;
}

.test-card-task {
    position: relative;
    z-index: 2;
    margin-top: 100px;
    background: rgba(0, 0, 0, 0.1);
    border-radius: 50px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    -o-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}

.test-card-task:hover {
    -o-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    background: rgba(0, 0, 0, 0.234);
}

.test-card-task-header {
    text-align: center;
    padding-top: 25px;
    font-weight: 400;
    font-size: 30px;
    line-height: 36px;
    color: #FFFFFF;
}

.test-card-task-text {
    margin-top: 53px;
    font-weight: 400;
    font-size: 22px;
    line-height: 27px;
    color: #FFFFFF;
    max-width: 840px;
}

.pass-the-test {
    margin: 58px auto 50px;
    text-align: center;
    padding: 10px 30px;
    font-weight: 500;
    font-size: 15px;
    line-height: 25px;
    color: #000000;
    background: #FFFFFF;
    border-radius: 50px;
    border: 1px solid #478E64;
    width: 160px;
    -o-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}

.pass-the-test:hover {
    border: 1px solid #000;
    background: #1a8a49;
    -o-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}

.test-card-task-img {
    background: url(../../img/test-img.png) center center/cover no-repeat;
    width: 225px;
    height: 160px;
}

.road {
    position: relative;
    z-index: 0;

}

.road-img {
    background: url(../../img/road.png) center center/cover no-repeat;
    width: 626px;
    height: 576px;
    position: absolute;
    top: -475px;
    right: -110px;
}

@media (max-width: 1024px) {
    .test-block {
        padding-bottom: 30px;
    }

    .trafficlight {
        width: 48px;
        height: 91px;
        top: 35px;
        left: 40px;
    }

    .test-block-header {
        font-weight: 700;
        font-size: 21px;
        line-height: 25px;
        color: #FFFFFF;
        padding: 25px 0 0;
    }

    .test-block-subheader {
        font-weight: 600;
        font-size: 12px;
        line-height: 20px;
        text-align: center;
        color: #FFFFFF;
        max-width: 358px;
        padding: 5px 0 30px;
    }

    .test-card-practice {
        flex-direction: column-reverse;
    }

    .test-card-practice-header {
        font-weight: 400;
        font-size: 12px;
        line-height: 15px;
        color: #FFFFFF;
    }

    .test-card-practice-img {
        width: 117px;
        height: 85px;
        margin-bottom: 25px;
    }
    
    .test-card-practice-text:last-child {
        margin-bottom: 25px;
    }

    .test-card-practice-text {
        font-weight: 400;
        font-size: 12px;
        line-height: 15px;
        color: #FFFFFF;
    }

    .test-card-task {
        margin-top: 30px;
        flex-direction: column-reverse;
    }

    .test-card-task-header {
        font-weight: 400;
        font-size: 12px;
        line-height: 15px;
        color: #FFFFFF;
    }

    .test-card-task-img {
        width: 117px;
        height: 85px;
        margin-bottom: 25px;
    }

    .test-card-task-text {
        font-weight: 400;
        font-size: 12px;
        line-height: 15px;
        color: #FFFFFF;
        margin: 30px 15px 0;
        text-align: center;
    }

    .pass-the-test {
        font-size: 12px;
        line-height: 25px;
        color: #000000;
        width: 80px;
        margin: 28px auto 25px
    }

    .road-img {
        width: 190px;
        height: 175px;
        top: -129px;
        right: -16px;
    }
}