.container {
    margin: 0 auto;
    max-width: 1200px;
}

.header-block {
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo-block {
    display: flex;
    align-items: center;
}

.logo {
    background: url(../../icons/Logo.svg) center center/cover no-repeat;
    width: 20px;
    height: 25px;
}

.logo-text {
    background: url(../../icons/logo-header.svg) center center/cover no-repeat;
    width: 165px;
    height: 15px;
    margin-left: 5px;
}

.social-header-block {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.social-header-block .instagram-item {
    background: url(../../icons/instagram-black.svg) center center/cover no-repeat;
    width: 20px;
    height: 20px;
}

.social-header-block .whatsapp-item {
    background: url(../../icons/whatsapp-black.svg) center center/cover no-repeat;
    width: 20px;
    height: 20px;
}

.social-header-block .telegram-item {
    background: url(../../icons/telegram-black.svg) center center/cover no-repeat;
    width: 20px;
    height: 20px;
    margin-right: 256px;
}

.lang {
    background: url(../../icons/language.svg) center center/cover no-repeat;
    width: 20px;
    height: 20px;
    margin-right: 21px;
    position: relative;
}

.lang-menu {
    display: none;
    margin-top: 20px;
    width: 130px;
    height: 70px;
    background: white;
    border-radius: 10px;
    position: absolute;
    z-index: 1;
}

.lang-menu:hover {
    display: block;
}

.lang:hover .lang-menu {
    display: block;
    background: #F0EAD2;
}

.lang-menu-item:hover .lang-menu {
    display: block;
}


.lang-menu-item {
    cursor: pointer;
    background: none;
    border: none;
    padding: 5px;
    font-weight: 500;
    font-size: 18px;
    line-height: 20px;
    color: #000000;
}

.lang-menu-item:hover {
    color: green;
}

.admin {
    background: url(../../icons/admin_panel_settings.svg) center center/cover no-repeat;
    width: 20px;
    height: 20px;
}




@media (max-width: 1024px) {
    .header-block {
        display: none;
    }

    .header-block-mobile {
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    .logo-block-mobile {
        display: flex;
        align-items: center;
    }

    .logo-mobile {
        background: url(../../icons/logo-header-white.svg) center center/cover no-repeat;
        width: 19px;
        height: 25px;
    }

    .logo-text-mobile {
        background: url(../../icons/Logo-white.svg) center center/cover no-repeat;
        width: 165px;
        height: 15px;
        margin-left: 5px;
    }

    .lang-menu {
        left: -75px;
    }

    .lang-mobile {
        background: url(../../icons/language.svg) center center/cover no-repeat;
        width: 20px;
        height: 20px;
        margin-right: 21px;
        position: relative;
    }

    .lang-mobile:hover .lang-menu {
        display: block;
        background: #F0EAD2;
    }
}